$color-primary: #004a7c;
$color-secondary: #005691;
$color-tertiary: #e8f1f5;
$color-white: #fafafa;
$color-grey: #E1E1E1;
$color-tertiary-medium: #F0F9FD;

.resume {
    box-sizing: border-box;
    background: linear-gradient(180deg, #E8F1F5 0%, #005691 100%);
    position: relative;
    margin-top: 5rem;

    &__svg {
        position: absolute;
        width: 9rem;
        height: auto;
        &-cap{
            left: 20%;
            top: 33%;
            @media only screen and (max-width: 600px) {
                left: 10%;
            }
        }

        &-work {
            right: 50%;
            top: 37%;
            width: 7rem;
            @media only screen and (max-width: 600px) {
                right: 40%;
                width: 5rem;
            }
        }

        &-runner {
            right: 30%;
            bottom: 20%;
            width: 6rem;
            @media only screen and (max-width: 600px) {
                right: 5%;
                width: 4rem;
            }
        }

        &-contact {
            right: 20%;
            top: 30%;
            width: 7rem;
            @media only screen and (max-width: 600px) {
                right: 10%;
                width: 5rem;
            }
        }

        &-resume {
            left: 30%;
            bottom: 30%;
            @media only screen and (max-width: 600px) {
                left: 15%;
                width: 7rem;
            }
        }
    }

    &-container {
        display: flex;
        flex: 1;
        overflow-x: hidden;
        overflow-y: visible;
        display: grid;
        grid-template-columns: repeat(6, min-content);
        grid-template-rows: repeat(2, 1fr) 50px repeat(2, 1fr);
        justify-content: flex-start;
        max-width: 1000px;
        margin: auto;
        background: linear-gradient(180deg, $color-tertiary-medium 0%, #005691 100%);
        border-radius: 10px;
        padding: 2rem;
        box-sizing: border-box;

        @media only screen and (max-width: 600px) {
            overflow-x: scroll;
        }

        

    }

    &__item {
        grid-row: 3/6;
        display: flex;
        flex-direction: column;

        &:nth-child(2n) {
            grid-row: 1/4;
            flex-direction: column-reverse;
        }

        &-svg {
            svg {
                height: 100px;
                width: auto;
            }
        }

        &-chevron {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
        
            & svg {
                //same as declared in row template
                height: 50px;
                width: auto;
                fill: inherit;
            }
        }

        &-date {
            font-size: 1.6rem;
            position: absolute;
            right: 44%;
            &:nth-child(2n) {
                color: $color-primary;
            }

            @media only screen and (max-width: 600px) {
                font-size: 1.4rem;
            }

            
        }

        .dotted-line {
            height: 50px;
            width: 5px;
            border: none;
        }

        &-content {
            display: flex;
            justify-content: center;
            border-radius: 5px;
            position: relative;
            align-items: center;

            &-desc {
                background-color: $color-white;
                width: 100%;
                margin: 1rem;
                border-radius: 5px;
                opacity: 0;
                transition: opacity 0.3s;
    
                p {
                    font-size: 1.4rem;
                    margin: .5rem;
                    font-family: 'Montserrat';
                }
            }

            &:hover {
                & > * {
                    opacity: 1;
                    background-color: red;
                }
            }
        }

        &-header {
            text-align: center;
            padding: 1rem;
            border-radius: 5px;
            white-space: nowrap;
            margin: 0rem;
            font-size: 1.6rem;
        }

    }

}