$color-primary: #004a7c;
$color-secondary: #005691;
$color-tertiary: #e8f1f5;
$color-white: #fafafa;
$color-tertiary-medium: #F0F9FD;

html {
    font-size: 10px;

    @media only screen and (max-width: 800px) {
        font-size: 8px;
    }

    @media only screen and (max-width: 600px) {
        font-size: 6px;
    }
}

body {
background-color: $color-tertiary;
margin: 0px;
}

h1, h2, label, button, li, strong {
    font-family: 'Libre Baskerville', 'serif';
    font-weight: 400;
}

p, input, textarea, a{
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
}

b {
   background-color: $color-secondary;
   padding: 1rem;
   font-weight: bold;
   border-radius: 1rem 1rem 1rem 1rem;
}

.row {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    overflow-x: hidden;
    overflow-y: visible;
    max-width: 1000px;
    margin: auto;
    width: 100%;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.col {
    flex: 1;
    &-1 {
        flex: .1;
    }

    &-2 {
        flex: .2;
    }

    &-4 {
        flex: .4;
    }

    &-5 {
        flex: .5;
        

    @media only screen and (max-width: 800px) {
        flex: 1;
        display: flex;
        justify-content: center;
        width: 100vw;
        }

    }

    &-7 {
        flex: .7;
    }

    &-9 {
        flex: .9;
        @media only screen and (max-width: 800px) {
            flex: 1;
    }
    }
}


.justify-content {
    &-space-evenly {
        justify-content: space-evenly;
    }
    &-center {
        justify-content: center;
    }

    &-end {
        justify-content: flex-end;
    }
    &-space-between{
        justify-content: space-between;
    }
}

.align-items {
    &-center {
        align-items: center;
    }
}

.direction-column {
    flex-direction: column;
}

.align-self{
    &-center {
        align-self: center;
    }
}

.m-left{
    &-1 {
        margin-left: 1px;
    }
    &-2 {
        margin-left: 2px;
    }
    &-3 {
        margin-left: 3px;
    }

    &-4 {
        margin-left: 4px;
    }
    
}

.p {
    &-1 { padding: 1rem }
    &-2{ padding: 2rem }

    &r {
        &-1 {
            padding-right: 1rem;
        }
    }
}

.flex {
    display: flex;
}

.text-align {
    &-center {
        text-align: center;
    }
}

.underline {
    text-decoration: underline;
}

.position {
    &-relative {
        position: relative;
    }

    &-absolute {
        position: absolute;
    }
}

//keyframes
@keyframes fade-in {
    0% {
        background-color: transparent;
    }

    100% {
        background-color: $color-secondary;
    }
}

@keyframes fade-out {
    0% {
        background-color: $color-secondary;
    }

    100% {
        background-color: 'transparent';
    }
}

@keyframes show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes hide {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes rotateLink {
    0% {
        transform: rotate(-28deg);
    }

    100% {
        transform: rotate(0deg);
    }
}

@keyframes rotateBackLink {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-28deg);
    }
}

@keyframes rotateHabts {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-20deg);
    }
}

@keyframes rotateBackHabts {
    0% {
        transform: rotate(-20deg);
    }

    100% {
        transform: rotate(0deg);
    }
}

//sections

.section-title {
        font-size: 4rem;
        color: $color-primary;
}

.background {
    &-white {
        background-color: $color-secondary;
    animation-name: fade-in;
    animation-duration: .5s;
    animation-timing-function: ease-out;
    }

    &-transparent {
        background-color: transparent;
        animation-name: fade-out;
        animation-duration: .5s;
        animation-timing-function: ease-out;
    }
}


.navbar {
    position: fixed;
    width: 100%;
    z-index: 10000;
    padding: 0rem 4rem 0rem 4rem;
    box-sizing: border-box;

    .logo {
        margin-right: 0px;

        &:hover {
            cursor: pointer;
        }
    }

    strong {
        font-size: 1.6rem;
        color: $color-white;
        padding: 1rem;
        border-radius: 5px;
        margin-block-start: 1em;
    margin-block-end: 1em;
    &:hover { 
        cursor: pointer;
    }
    }

    .menu strong {
        &:hover {
            cursor: pointer;
            background-color: $color-tertiary;
            color: $color-primary;
        }
    }

    @media only screen and (max-width: 1200px) {
        .nav-divider {
            display: none;
        }

        justify-content: center;

        padding: 1rem 1rem 1rem 1rem;
    }

    .chevron {
        position: relative;
        text-align: center;
        height: 1.5rem;
        width: 1.5rem;

        &-flipped {
            -webkit-transform: rotate(-90deg);
            -moz-transform: rotate(-90deg);
            -ms-transform: rotate(-90deg);
            -o-transform: rotate(-90deg);
            transform: rotate(-90deg);
            right: -6px;
            top: 1px;
        }

        &-initial {
            -webkit-transform: rotate(90deg);
            -moz-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
            -o-transform: rotate(90deg);
            transform: rotate(90deg);
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 20%;
            width: 51%;
            background: $color-tertiary;
            -webkit-transform: skew(0deg, 40deg);
            -moz-transform: skew(0deg, 40deg);
            -ms-transform: skew(0deg, 40deg);
            -o-transform: skew(0deg, 40deg);
            transform: skew(0deg, 40deg);
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            height: 20%;
            width: 50%;
            background: $color-tertiary;
            -webkit-transform: skew(0deg, -40deg);
            -moz-transform: skew(0deg, -40deg);
            -ms-transform: skew(0deg, -40deg);
            -o-transform: skew(0deg, -40deg);
            transform: skew(0deg, -40deg);
        }
    }

    
}

.vector-header {
    position: absolute;
    top: 0px;
    left: -50px;
    z-index: 100;
}

@keyframes rotateIn {
    0% {
        transform: translate(441.576px, 8439.33px) rotate(0deg) scale(1);
    }

    100% {
        transform: translate(800px, 8300px) rotate(-80deg) scale(.5);
    }
}


@keyframes rotateOut {
    0% {
        transform: translate(800px, 8300px) rotate(-80deg) scale(.5);
    }

    100% {
        transform: translate(441.576px, 8439.33px) rotate(0deg) scale(1);
    }
}

@keyframes cursor {
    0% {
        width: .5rem;
        opacity: 0;
    }

    50% {
        width: .5rem;
        opacity: 1;
    }

    100% {
        width: .5rem;
        opacity: 0;
    }
}

@keyframes showText {
    0% {
        width: 0%
    }
    100% {
        width: 100%;
    }
}

.header {
    background-color: $color-primary;
    min-height: 80vh;
    display: flex;
    flex-wrap: nowrap;
    padding-top: 5rem;
    position: relative;


    .avatar {
        height: auto;
        width: 90%;

        @media only screen and (max-width: 900px) {
            width: 100%;
            height: auto;
        }

    }

    &-text {
        box-sizing: border-box;
        padding: 5rem;

        & > :nth-child(3) {
            position: relative;
            top: -10%;
            right: -40%;
        }
        @media only screen and (max-width: 900px) {
            padding: 1rem;
        }
    }

    & > .col {
        flex: .5
    }

    h1  {
        font-size: 15rem;
        margin: 0rem;
        width: 100%;
        text-align: right;
        font-weight: 700;
        background-clip: text;
        -webkit-background-clip: text;
        -moz-background-clip: text;
        -webkit-text-fill-color: transparent; 
        -moz-text-fill-color: transparent;
        background-image: linear-gradient(89.79deg, #0162A4 -16.09%, #82CCFF 103.85%);

        @media only screen and (max-width: 900px) {
            font-size: 8rem;
            text-align: right;
        }
    }

    p {
        font-size: 2rem;
        color: $color-white;
        z-index: 1000;
        margin: 2rem;
    }
    

}

.d-curve {
    &:before {
        content: '';
        background-color: $color-primary;
        width: 100%;
        height: 10rem;
        border-bottom-right-radius: 100%;
    }
}

.expertise {
    padding-bottom: 2rem;

    &__content {
        background-color: $color-tertiary-medium;
        padding: 5rem;
        border-radius: 10px;
    }
    
    &-item {
        background-color: $color-secondary;
        color: $color-white;
        display: flex;
        flex-direction: column;
        border-radius: 10px;
        width: 100%;
        align-items: center;
        padding: 3rem;
        margin: 2rem;
        box-sizing: border-box;

        @media only screen and (max-width: 600px) {
            min-width: 250px;
         }

         h2 {
             font-size: 2rem;
             text-align: left;
             width: 100%;
             margin: 1rem 0rem 0rem 0rem;
             border-bottom: 1px solid $color-primary;
         }

        p {
            font-size: 1.6rem;
            margin: .5rem 0rem 0rem 0rem;
        }

        & svg {
            fill: $color-white;
            height: 5rem;
            width: auto;
        }

        .custom-svg {
            height: 5rem;
            width: auto;
        }
    }
}

.projects {

    @keyframes hoverImageShow {
        from {
            left: 0%;
            width: 30%;
        }

        to {
            left: 40%;
            width: 30%;
        }
    }

    &__content {
        background-color: $color-tertiary-medium;
        border-radius: 10px;
        padding: 5rem;
        & > .col:not(:nth-child(3)) {
            margin-top: 1rem;
            position: relative;
            & > .row {
                & > .col {
                    &:hover {
                        img {
                            position: absolute;
                            left: 40%;
                            top: 0;
                            width: 30%;
                            height: auto;
                            z-index: 1000;
                            animation-name: hoverImageShow;
                            animation-duration: .7s;
                            animation-timing-function: ease-out;
                        }
                    }
                }
            }
        }
    }

    h2 {
        color: $color-primary;
        font-size: 2rem;
        text-align: left;
        width: 100%;
        margin: 0rem;
    }

    p {
        font-size: 1.6rem;
        margin: .5rem 0rem 2rem 0rem;
        color: $color-primary;
    }

    &__item-header {
        align-items: flex-end;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid $color-tertiary;
        margin: 2rem 0rem 1rem 0rem;
        p {
                    font-size: 1.4rem;
        color: $color-primary;
        width: max-content;
        white-space: nowrap;
        margin: 0rem;
        }
    }


    img {
        width: 100%;
        height: auto;
    }

}

.contact {
    background-color: $color-secondary;
    padding-top: 3rem;
    padding-bottom: 3rem;

    .form {
        display: flex;
        flex: 1;
        flex-direction: column;
        min-width: 300px;
        background-color: $color-primary;
        box-sizing: border-box;
        padding: 2rem;
        border-radius: 10px;

        h1 {
            margin: 0px;
            color: $color-white;
            margin: 2rem 0rem 1rem 0rem;
        }

        p {
            font-size: 1.6rem;
            margin: 2rem;
            color: $color-tertiary;
        }

        label {
            font-size: 1.4rem;
            color: $color-tertiary;
            margin: 1rem;
        }

        input, textarea {
            border: none;
            border-radius: 10px;
            background-color: $color-secondary;
            width: 95%;
            padding: 1rem;
            color: $color-tertiary;
            font-size: 1.6rem;
            margin-top: 1rem;

            &::placeholder{
                color: rgba(0, 74, 124, 0.2);
            }
        }
        textarea {
            height: 100px;
        }

        button {

            @keyframes expand {
                0% {
                    width: 20%;
                }
            
                100% {
                   width: 30%;
                }
            }

            @keyframes contract {
                0% {
                    width: 30%;
                }
            
                100% {
                   width: 20%;
                }
            }

            background-color: $color-white;
            margin: 2rem 0rem 2rem 0rem;
            border-radius: 10px;
            padding: 1rem;
            width: 20%;
            align-self: center;
            font-size: 1.6rem;
            color: $color-primary;
            border-style: none;
            animation-name: contract;
            animation-duration: .5s;
            animation-timing-function: ease-out;

            &:hover {
                cursor: pointer;
                width: 30%;
                animation-name: expand;
                animation-duration: .5s;
                animation-timing-function: ease-out;
            }
        }
    }
}

